.header-section{
  padding: 0px 3rem;
  justify-content: space-between;
}
.header-title{
  color: #495567;
}
.hamburger-menu-section{
  padding:0 2rem;
}
.hamburger-menu-options{
  margin: 1rem 0;
  color: black;
}
.hamburger-menu-options:hover{
  color: orange ;
}
.hamburger-icon{
  cursor: pointer;
}
.header-option{
  color: black;
}
.header-option:hover{
  color: orange;
}
.btn{
  padding: 0.8rem 2rem;
  background-color: orange;
  border-radius: 0px;
  border-style: solid;
  border-color: transparent;
  color: white;
  font-size: 1rem;
  font-weight: bold;
}
.btn:hover{
  background-color: transparent;
  border-color: orange;
  color: orange;
  cursor: pointer;  
}
.hamburger-menu-btn{
  margin: 1rem auto;
}

/*HOME SECTION CSS*/


/*Home About*/
.home-about{
  padding: 6rem 0;
  color: white;
  background-image: url(/src/assets/images/home-hero-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 40rem;
  align-content: center;
}
.home-title-text{
  width: 60%;
  margin: auto;
  font-size: 2.5rem;
}
.home-text{
  width: 80%;
  margin: 1rem auto;
}
.home-btn{
  margin: 1rem auto;
}


/*Home How*/
.home-how{
  margin: 12rem auto;
  text-align: center;
}
.how-text-heading,.how-text,.feature-text-header,.feature-text,.career-about-text,.job-title,.job-location{
  text-align: center;
  width: 80%;
  margin: auto;
}
.how-text-heading,.feature-text-header,.career-about-header,.job-title{
  font-size: 1.5rem;
  font-weight: bold;
}

/*Home Feature*/
.home-feature{
  width: 100%;
}
.home-feature-img,.about-feature-img,.about-value-img,.location-img,.career-img{
  display: block;
  border-radius: 50%;
  width: 50%;
  margin: 5rem auto 2rem  auto;
}
.home-feature-btn{
  display: block;
  margin: 2rem auto;
}
.feature-text,.feature-text-header,.career-about-header{
  margin: 2rem auto;
}
.feature-text-header,.career-about-header,{
  width: 60%;
  font-size: 2.5rem;
  text-align: center;
}

/*Home Footer*/
.home-footer{
    background-color: #495567;
    background-image: url(/src/assets/patterns/cta-circles.svg);
    background-repeat: no-repeat;
    color: white;
    text-align: center;
    padding: 2rem 0;
}
.home-footer-text{
  font-size: 2.5rem;
  width: 20rem;
  margin: 2rem auto;
  padding: 10rem auto;
}
.home-footer-appstore{
  margin: 0 0.5rem;
}
.home-footer-googleplay{
  margin: 0 0.5rem;
}

/*About Section*/

.about-header{
  background-image: url(/src/assets/images/about-hero-mobile.jpg);
  height: 30vh;
  text-align: center;
}
.about-detail-section{
  margin-top: 5%;
}
.about-header-text{
  padding: 3rem 0;
  color: rgb(236, 232, 232);
  font-size: 3rem;
  font-weight: bold;
}
.about-feature-text-header{
  font-size: 2rem;
  width: 60%;
  margin: 0 auto;
  text-align: center;
} 
.about-feature-text{
  text-align: center;
  width: 80%;
  margin: 0 auto;
}
.about-value-header-text{
  text-align: center;
  font-size: 3rem;
}
.about-value-text-header{
  text-align: center;
  font-size: 2rem;
}
.about-value-text{  
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

.faqs-header{
  display: block;
  font-size: 3rem;
  text-align: center;
  margin: 15% auto 5% auto;
}
.faqs-title{
  font-size: 2rem;
  text-align: center;
  margin: 0 auto 5% auto;
}
.faqs-div{
  width: 80%;
  background-color: #F2F5F9;
  padding: 2rem;
  margin: 2rem auto;
  border-radius: 3px;
}
.faq-question{
  font-size: 1.2rem;
}
.faq-question:hover{
  cursor: pointer;
}
.faq-answer{
  margin: 3rem auto;
}

/*Location*/

.location-header{
  background-image: url(/src/assets/images/careers-location-hero-mobile.jpg);
  height: 30vh;
  text-align: center;
}
.location-img{
  display: block;
  width: 80%;
  margin: 3rem auto;
}
.city-name{
  font-size: 1.5rem;
  font-weight: bold;
  width: 50%;
  padding: 1rem 2rem;
  text-align: center;
  margin: 1.3rem auto;
  background-color: pink;
}
.location-main{
  width: 80%;
  margin:4rem auto;
  text-align: center;
}
.location-main-header{
  font-size: 3rem;
  width: 70%;
  margin:1rem auto;
  font-weight: bold;
}
.location-main-text{
  text-align: left;
  margin: 2rem auto;
}

.job-descp-div{
  width: 90%;
  background-color: #F2F5F9;
  margin: 1.5rem auto;
  padding: 2rem 3rem;
}
.career-btn{
  display: block;
  margin: 2rem auto;
}
.job-btn{
  display: block;
  margin: 1rem auto;
}
.career-about-header{
  text-align: center;
}

@media screen and (min-width:768px) and (max-width: 1024px) {



  .header-options-div{
    margin: 0 2rem;
  }

/*HOME SECTION CSS*/

/*Home About*/
  .header-section{
    padding: 0px 5rem;
  }

  .home-about{
    background-image: url(/src/assets/images/home-hero-tablet.jpg);
  }
  .home-title-text{
    width: 40%;
  }
  .home-text{
    width: 50%;
  }

  /*Home How*/
  .home-how{
    width: 100%;
    padding: 0px 5rem;
    text-align: left;
  }
  .how-text-heading,.how-text{
    text-align: left;
    width: 100%;
    margin: 5% 8%;
  }

  /*Home Feature*/
  .home-feature-div{
    width: 80%;
    margin: auto;
  }

  .about-header{
    background-image: url(/src/assets/images/about-hero-tablet.jpg);
  }
  .about-feature-text-div{
    width: 100%;
  }
  .about-header-text{
    padding:  5rem 10%;
    text-align: left;
  }
  .about-feature-img{
    width: 40%;
  }
  .about-feature-text-header{
    width: 45%;
    font-size: 3rem;
  }
  .about-feature-text{
    width: 60%;
    margin: 3% auto;
  }
  .about-detail-div{
    width: 80%;
    margin: auto;
    justify-content: space-between;
  }
  .about-value-img{
    width: 50%;
  }
  .about-value-text{
    margin: 1rem auto;
  }

  .location-main-header{
    width: 100%;
    word-spacing: 1.2rem;
  }
  .location-main-text{
    width: 60%;
  }

}

